package com.nomanr.animate.compose.presets.fadeinentrances

import androidx.compose.runtime.Composable
import androidx.compose.runtime.State
import androidx.compose.ui.Modifier
import com.nomanr.animate.compose.core.AnimationPreset
import com.nomanr.animate.compose.core.Easings
import com.nomanr.animate.compose.core.Keyframe
import com.nomanr.animate.compose.core.TransformProperties
import com.nomanr.animate.compose.core.animateKeyframe

class FadeIn : AnimationPreset {

    private val ease = Easings.SoftLanding

    private val keyframes = listOf(
        Keyframe.Segment(
            start = 0f, end = 1f,
            from = TransformProperties(alpha = 0f),
            to = TransformProperties(alpha = 1f),
            easing = ease
        )
    )

    @Composable
    override fun animate(progress: State<Float>): Modifier =
        Modifier.animateKeyframe(
            progress = progress,
            keyframes = keyframes
        )
}